.back-button-alignment {
    width: 32px;
    height: 32px;
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 10px;
    left: 20px;
    z-index: 1001;
}

.back-button-alignment img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}